import React from 'react';
import { connect } from 'react-redux';
import { selectDice } from '../../actions/actionsDice';

const mapStateToProps = (state) => {
    return ({
        dices: state.Dices,
    })
}
const mapDispatchToProps = (dispatch) => {
    return ({ selectDice: (a) => dispatch(selectDice(a)) })
}

const Dice = ({ dices, selectDice }) => {
    let keys = Object.keys(dices);
        return (
            keys.map(k => {
                return (
                    <img
                        alt='dice'
                        src={dices[k].img}
                        className="tc flex-wrap inline-flex ma2 grow grow:hover pointer"
                        key={dices[k].key}
                        style={dices[k].style}
                        onClick={() => selectDice(dices[k])} />
                )
            }))
}

export default connect(mapStateToProps, mapDispatchToProps)(Dice);